.search-bar {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
    margin-bottom: 2rem;
  
    &__input {
      font-size: 1.5rem;
      padding: 0.5rem;
      margin-right: 1rem;
      border: 1px solid #ccc;
      border-radius: 4px;
    }
  
    &__button {
      font-size: 1.5rem;
      background-color: #0077be;
      color: #fff;
      padding: 0.5rem 1rem;
      border: none;
      border-radius: 4px;
      cursor: pointer;
  
      &:hover {
        background-color: #0066a3;
      }
    }
  }
  
  