.recommendation-container {
    background-color: #f5f5f5;
    padding: 2rem;
    border-radius: 10px;
    margin-top: 2rem;
  
    h2 {
      font-size: 2rem;
      margin-bottom: 1rem;
      text-align: center;
    }
  
    p {
      font-size: 1.2rem;
      margin: 0.5rem 0;
    }
  
    /* Рекомендации по одежде для холодной погоды */
    &.cold {
      background-color: #a6b2c1;
      color: white;
  
      p {
        font-weight: bold;
      }
    }
  
    /* Рекомендации по одежде для теплой погоды */
    &.warm {
      background-color: #ffcc99;
  
      p {
        font-style: italic;
      }
    }
  
    /* Рекомендации по одежде для жаркой погоды */
    &.hot {
      background-color: #ff6666;
      color: white;
  
      p {
        text-decoration: underline;
      }
    }
  }